:root {
  --header-link-height: 60px;
  --logo-margin-right: 51px;
  --item-spacing: 30px;
  --blue-color: #00acdc;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
}

header {
  border-bottom: 1px solid #e5e5e5;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.container {
  align-items: center;
  width: 100%;
  max-width: 1200px;
  padding: 0 49px 0 29px;
  display: flex;
}

.logo-link {
  margin-right: var(--logo-margin-right);
}

.logo {
  height: 40px;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: var(--item-spacing);
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: var(--header-link-height);
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 11px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__link.is-active {
  color: var(--blue-color);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background: var(--blue-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
